<template>
    <div id="content">
        <el-row v-if="mobile">
            <el-col :span="6">
                <div class="grid-content bg-purple logo">
                    <img src="../../assets/logoNew.png" />
                    <span>{{i18n.$t('home').mytitle}}</span>
                </div>
            </el-col>
            <el-col :span="16">
                <div class="grid-content bg-purple-light">
                    <el-menu
                        :default-active="path"
                        class="el-menu-demo"
                        mode="horizontal"
                        @select="handleSelect"
                        router="true"
                    >
                        <el-menu-item index="/home">{{i18n.$t('home').productIntroduction}}</el-menu-item>
                        <el-menu-item index="/">{{i18n.$t('home').operationManual}}</el-menu-item>
                        <el-menu-item index="/reception">{{i18n.$t('home').reception}}</el-menu-item>
                    </el-menu>
                </div>
            </el-col>
            <el-col :span="2">
                <el-dropdown style="position:absolute;right:10px;top:10px;">
                    <el-button>
                        {{i18n.$t('home').switchLanguage}}
                        <i
                            class="el-icon-arrow-down el-icon--right"
                        ></i>
                    </el-button>
                    <template #dropdown>
                        <el-dropdown-menu>
                            <el-dropdown-item @click="switchLanguage('cn')">中文</el-dropdown-item>
                            <el-dropdown-item @click="switchLanguage('en')">English</el-dropdown-item>
                            <el-dropdown-item @click="switchLanguage('jp')">日本語</el-dropdown-item>
                        </el-dropdown-menu>
                    </template>
                </el-dropdown>
            </el-col>
        </el-row>
        <div v-else class="menu">
            <div class="title">
                <div>
                    <img src="../../assets/LOGOAI-01.png" />
                    <span>{{i18n.$t('home').mytitle}}</span>
                </div>
                <img src="../../assets/more.png" @click="show" />
            </div>
            <div class="titleItem">
                <router-link to="/home" @click="show">产品介绍</router-link>
            </div>
            <div class="titleItem">
                <router-link to="/bbbb" @click="show">使用手册</router-link>
            </div>
            <div class="titleItem">
                <router-link to="/reception" @click="show">咨询处</router-link>
            </div>
        </div>
        <router-view />
    </div>
</template>

<script>
import { useI18n } from "../../i18nPlugin";

export default {
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    data() {
        return {
            path: "",
            mobile: !this.$store.state.viewListen
        };
    },
    methods: {
        switchLanguage(params) {
            console.log(params);
            window.localStorage.setItem("language", params);
            this.i18n.locale.value = params;
        },
        handleSelect(key) {
            this.path = key;
            window.localStorage.setItem("path", key);
        },
        show() {
            if (!document.getElementsByClassName("biu")[0]) {
                let e = document.getElementsByClassName("menu")[0];
                e.classList.remove("overflow");
                e.classList.add("biu");
            } else {
                let e = document.getElementsByClassName("biu")[0];
                e.classList.remove("biu");
                setTimeout(() => {
                    e.classList.add("overflow");
                }, 1000);
            }
        },
        push(path) {
            this.$router.push(path);
        }
    },
    mounted() {
        this.path =
            window.localStorage.getItem("path") == null
                ? "/home"
                : window.localStorage.getItem("path");
        console.log(this.$store.state.viewListen, "盒子");
    },
    watch: {
        $route: {
            handler: function(val) {
                this.handleSelect(val.href);
            }
        }
    }
};
</script>
<style scoped lang="scss">
@media (min-width: 600px) {
    #content {
        min-width: 1150px;
    }
    .logo {
        display: flex;
        align-items: center;
        height: 100%;
        padding-left: 100px;
        img {
            width: 40px;
        }
        span {
            font-weight: bold;
            font-size: 17px;
        }
    }
    .el-menu-item {
        font-weight: bold;
    }
    .el-menu.el-menu--horizontal {
        border-bottom: 0px;
    }
    .el-menu--horizontal > .el-menu-item.is-active {
        border-bottom: 2px solid #1fab89;
    }
    .el-col {
        border-bottom: 2px solid #e6e6e6;
    }
}
@media all and (max-width: 600px) {
    .menu {
        background-color: #1fab89;
        padding: 8px;
        height: 30px;
        overflow: hidden;
        .title {
            color: #ffffff;
            display: flex;
            align-items: center;
            justify-content: space-between;
            div {
                display: flex;
                align-items: center;
            }
        }
        .titleItem {
            padding: 5px;
            color: #ffffff;
            border-bottom: 1px solid #ffffff;
            a {
                text-decoration: none;
                color: #ffffff;
                font-weight: normal;
            }
            .router-link-active {
                color: #ffba5a;
            }
        }
    }
    .biu {
        animation: showw 0.2s;
        animation-fill-mode: forwards;
    }
    .overflow {
        animation: over 0.2s;
        animation-direction: alternate;
        animation-fill-mode: forwards;
    }
    @keyframes showw {
        0% {
            height: 30px;
        }
        100% {
            height: 130px;
        }
    }
    @keyframes over {
        0% {
            height: 130px;
        }
        100% {
            height: 30px;
        }
    }
}
</style>
